





















import Vue from "vue";
export default Vue.extend({
  name: "LoginPage",
  data() {
    return {
      showErrorMessage: false,
      username: "",
    };
  },
  methods: {
    onLog: function () {
      if (this.username === "") {
        this.showErrorMessage = true;
        (
          document.getElementsByClassName("name-input")[0] as HTMLInputElement
        ).focus();
      } else {
        this.$router.push({ path: "/quiz" });
        this.$emit("logSuccess", this.username);
      }
    },
  },
  mounted() {
    (
      document.getElementsByClassName("name-input")[0] as HTMLInputElement
    ).focus();
  },
});
